"use client";

import { useState } from 'react';
import { useRouter } from 'next/navigation';
import { useAuth } from '@/contexts/AuthContext';
import { z } from 'zod';
import { toast } from 'sonner';
import { Shield, Loader2 } from 'lucide-react';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';


// Schemas
const loginSchema = z.object({
  email: z.string().email('Please enter a valid email address'),
  password: z.string().min(6, 'Password must be at least 6 characters'),
});

const signupSchema = z.object({
  name: z.string().min(2, 'Name must be at least 2 characters'),
  email: z.string().email('Please enter a valid email address'),
  password: z.string().min(6, 'Password must be at least 6 characters'),
  confirmPassword: z.string(),
}).refine((data) => data.password === data.confirmPassword, {
  message: "Passwords don't match",
  path: ["confirmPassword"],
});

export default function AuthPage() {
  const { signIn, signUp, user, isLoading: authLoading } = useAuth();
  const router = useRouter();

  const [activeTab, setActiveTab] = useState('login');
  const [isLoading, setIsLoading] = useState(false);

  // Form States
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');

  if (user && !authLoading) {
    router.replace('/');
    return null;
  }

  const handleLogin = async (e: React.FormEvent) => {
    e.preventDefault();
    setIsLoading(true);

    try {
      const { error } = await signIn(email, password);
      if (error) {
        if (error.message.includes('Invalid login credentials')) {
          toast.error('Invalid email or password');
        } else {
          toast.error(error.message);
        }
        return;
      }
      toast.success('Welcome back!');
      router.push('/');
    } catch (error: any) {
      toast.error('Authentication failed');
    } finally {
      setIsLoading(false);
    }
  };

  const handleSignup = async (e: React.FormEvent) => {
    e.preventDefault();
    setIsLoading(true);

    if (password !== confirmPassword) {
      toast.error("Passwords don't match");
      setIsLoading(false);
      return;
    }

    try {
      const { error } = await signUp(email, password, name);
      if (error) {
        toast.error(error.message);
        return;
      }
      toast.success('Account created! Welcome to ServiceDesk.');
      router.push('/');
    } catch (error: any) {
      toast.error(error.message || 'Signup failed');
    } finally {
      setIsLoading(false);
    }
  };

  if (authLoading) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-[#f8fafc]">
        <Loader2 className="h-8 w-8 animate-spin text-indigo-600" />
      </div>
    );
  }

  return (
    <div className="min-h-screen w-full flex">

      {/* Left Side - Brand (Desktop Only) */}
      <div className="hidden lg:flex w-1/2 bg-gradient-to-br from-indigo-600 to-indigo-900 flex-col justify-center items-center text-white p-12 relative overflow-hidden">
        {/* Decorative background element */}
        <div className="absolute top-0 left-0 w-full h-full opacity-10 bg-[url('https://www.transparenttextures.com/patterns/cubes.png')]"></div>

        <div className="relative z-10 flex flex-col items-center text-center space-y-6">
          <div className="w-24 h-24 rounded-3xl bg-white/10 backdrop-blur-sm flex items-center justify-center shadow-2xl border border-white/20">
            <Shield className="w-12 h-12 text-white fill-white/20" />
          </div>
          <h1 className="text-4xl font-bold tracking-tight">ServiceDesk</h1>
          <p className="text-xl text-indigo-100 font-medium max-w-md leading-relaxed">
            Streamline Your Support. <br />
            Manage service requests efficiently and effectively.
          </p>
        </div>
      </div>

      {/* Right Side - Form */}
      <div className="w-full lg:w-1/2 bg-white flex flex-col justify-center items-center p-8 relative">

        <Tabs value={activeTab} onValueChange={setActiveTab} className="w-full max-w-md">

          {/* Top Right Navigation Toggle */}
          <div className="absolute top-8 right-8 flex items-center gap-2 text-sm font-medium">
            <span className="text-slate-500">
              {activeTab === 'login' ? "Don't have an account?" : "Already have an account?"}
            </span>
            <TabsList className="bg-transparent p-0 h-auto space-x-0">
              <TabsTrigger
                value="signup"
                className={`text-indigo-600 hover:text-indigo-700 p-0 h-auto data-[state=active]:bg-transparent data-[state=active]:text-indigo-600 data-[state=active]:shadow-none ${activeTab === 'signup' ? 'hidden' : 'inline-flex'}`}
                onClick={() => setActiveTab('signup')}
              >
                Sign Up
              </TabsTrigger>
              <TabsTrigger
                value="login"
                className={`text-indigo-600 hover:text-indigo-700 p-0 h-auto data-[state=active]:bg-transparent data-[state=active]:text-indigo-600 data-[state=active]:shadow-none ${activeTab === 'login' ? 'hidden' : 'inline-flex'}`}
                onClick={() => setActiveTab('login')}
              >
                Sign In
              </TabsTrigger>
            </TabsList>
          </div>

          {/* Mobile Header (Visible on small screens) */}
          <div className="lg:hidden mb-10 flex flex-col items-center">
            <div className="w-12 h-12 rounded-xl bg-indigo-600 flex items-center justify-center mb-4 shadow-lg shadow-indigo-200">
              <Shield className="w-6 h-6 text-white fill-white" />
            </div>
            <h1 className="text-2xl font-bold tracking-tight text-slate-900">ServiceDesk</h1>
          </div>

          <TabsContent value="login" className="mt-0 space-y-8 animate-fade-in">
            <div className="space-y-2 text-center lg:text-left">
              <h2 className="text-3xl font-bold text-slate-900 tracking-tight">Welcome Back</h2>
              <p className="text-slate-500">Please enter your details to sign in.</p>
            </div>

            <form onSubmit={handleLogin} className="space-y-5">
              <div className="space-y-2">
                <label className="block text-sm font-semibold text-slate-900">Email Address</label>
                <input
                  type="email"
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                  placeholder="name@company.com"
                  required
                  className="w-full px-4 py-3 bg-white border border-slate-300 rounded-lg text-slate-900 placeholder:text-slate-400 focus:outline-none focus:ring-2 focus:ring-indigo-600 focus:border-transparent transition-all font-sans"
                />
              </div>

              <div className="space-y-2">
                <div className="flex items-center justify-between">
                  <label className="block text-sm font-semibold text-slate-900">Password</label>
                  <a href="#" className="text-sm text-indigo-600 hover:text-indigo-700 font-semibold transition-colors">
                    Forgot Password?
                  </a>
                </div>
                <input
                  type="password"
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                  placeholder="••••••••"
                  required
                  className="w-full px-4 py-3 bg-white border border-slate-300 rounded-lg text-slate-900 placeholder:text-slate-400 focus:outline-none focus:ring-2 focus:ring-indigo-600 focus:border-transparent transition-all font-sans"
                />
              </div>

              <button
                type="submit"
                disabled={isLoading}
                className="w-full flex justify-center py-3.5 px-4 bg-indigo-600 hover:bg-indigo-700 text-white rounded-lg font-bold text-base shadow-lg shadow-indigo-200 transition-all active:scale-[0.98] disabled:opacity-70 mt-4"
              >
                {isLoading ? <Loader2 className="h-5 w-5 animate-spin" /> : 'Sign In'}
              </button>
            </form>
          </TabsContent>

          <TabsContent value="signup" className="mt-0 space-y-8 animate-fade-in">
            <div className="space-y-2 text-center lg:text-left">
              <h2 className="text-3xl font-bold text-slate-900 tracking-tight">Create Account</h2>
              <p className="text-slate-500">Start managing your requests today.</p>
            </div>

            <form onSubmit={handleSignup} className="space-y-4">
              <div className="space-y-2">
                <label className="block text-sm font-semibold text-slate-900">Full Name</label>
                <input
                  type="text"
                  value={name}
                  onChange={(e) => setName(e.target.value)}
                  placeholder="John Anderson"
                  required
                  className="w-full px-4 py-3 bg-white border border-slate-300 rounded-lg text-slate-900 placeholder:text-slate-400 focus:outline-none focus:ring-2 focus:ring-indigo-600 focus:border-transparent transition-all font-sans"
                />
              </div>
              <div className="space-y-2">
                <label className="block text-sm font-semibold text-slate-900">Email Address</label>
                <input
                  type="email"
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                  placeholder="name@company.com"
                  required
                  className="w-full px-4 py-3 bg-white border border-slate-300 rounded-lg text-slate-900 placeholder:text-slate-400 focus:outline-none focus:ring-2 focus:ring-indigo-600 focus:border-transparent transition-all font-sans"
                />
              </div>
              <div className="space-y-2">
                <label className="block text-sm font-semibold text-slate-900">Password</label>
                <input
                  type="password"
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                  placeholder="••••••••"
                  required
                  className="w-full px-4 py-3 bg-white border border-slate-300 rounded-lg text-slate-900 placeholder:text-slate-400 focus:outline-none focus:ring-2 focus:ring-indigo-600 focus:border-transparent transition-all font-sans"
                />
              </div>
              <div className="space-y-2">
                <label className="block text-sm font-semibold text-slate-900">Confirm Password</label>
                <input
                  type="password"
                  value={confirmPassword}
                  onChange={(e) => setConfirmPassword(e.target.value)}
                  placeholder="••••••••"
                  required
                  className="w-full px-4 py-3 bg-white border border-slate-300 rounded-lg text-slate-900 placeholder:text-slate-400 focus:outline-none focus:ring-2 focus:ring-indigo-600 focus:border-transparent transition-all font-sans"
                />
              </div>

              <button
                type="submit"
                disabled={isLoading}
                className="w-full flex justify-center py-3.5 px-4 bg-indigo-600 hover:bg-indigo-700 text-white rounded-lg font-bold text-base shadow-lg shadow-indigo-200 transition-all active:scale-[0.98] disabled:opacity-70 mt-4"
              >
                {isLoading ? <Loader2 className="h-5 w-5 animate-spin" /> : 'Create Account'}
              </button>
            </form>
          </TabsContent>

        </Tabs>

        {/* Footer Links */}
        <div className="absolute bottom-8 flex gap-6 text-xs text-slate-400 font-medium">
          <a href="#" className="hover:text-slate-600 transition-colors">Terms of Service</a>
          <a href="#" className="hover:text-slate-600 transition-colors">Privacy Policy</a>
        </div>
      </div>
    </div>
  );
}
